@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
*{
  margin: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    font-family: "Poppins", sans-serif;
}
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #f9f9f9;
}
.header__logo {
    height: 40px;
    object-fit: contain;
}

.header__icon {
    padding: 20px;
}
.swipeButtons {
    position: fixed;
    bottom: 10vh;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}

.swipeButtons .MuiIconButton-root {
    background-color: white;
    box-shadow: 0px 10px 53px 0px rgba(0,0,0,0.3) !important;
}

.swipeButtons__repeat {
    padding: 3vw !important;
    color: #f5b748 !important;
}
.swipeButtons__left {
    padding: 3vw !important;
    color: #ec5e6f !important;
}
.swipeButtons__star {
    padding: 3vw !important;
    color: #62b4f9 !important;
}
.swipeButtons__right {
    padding: 3vw !important;
    color: #76e2b3 !important;
}

.swipeButtons__lightning {
    padding: 3vw !important;
    color: #915dd1 !important;
}
.card {
    position: relative;
    width: 600px;
    padding: 20px;
    max-width: 85vw;
    height: 50vh;
    border-radius: 20px;
    background-size: cover;
    background-position: center;
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}

.card > h3 {
    position: absolute;
    bottom: 10px; 
    color: white;
}
.tinderCards__cardContainer {
    display: flex;
    justify-content: center;
    margin-top: 5vh;
}

.swipe {
    position: absolute;
}

.chat {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    height: 70px;
    border-bottom: 1px solid #fafafa;
}
a {
    text-decoration: none; /* remove underline */
    color: inherit;
}
.chat__details {
    flex: 1 1;
}

.chat__details > p {
    color: gray;
}

.chat__image {
    margin-right: 20px;
}
.chat__timestamp {
    color: gray;
}

.chat {
    font-size: medium;
}

.chat__details > h2 {
    font-size: larger;
}
.chatScreen__message{
    display: flex;
    align-items: center;
    padding: 20px;
}

.chatScreen__paragraph {
    margin-left: 10px;
    background-color: lightgray;
    padding: 15px;
    border-radius: 20px;
}

.chatScreen__timpstamp {
    text-align: center;
    padding: 20px;
    color: gray;
}

.chatScreen__paragraphUser {
    margin-left: auto;
    background-color: #29b3cd;
    padding: 15px;
    border-radius: 20px;
    color: white; 
}

.chatScreen__input {
    display: flex;
    padding: 5px;
    position: fixed;
    bottom: 0;
    width: 100%;
    border-top: 1px solid lightgray;
}

.chatScreen__inputField {
    flex: 1 1;
    padding: 10px;
    border: none;
    outline: none;
}
